.library-header__container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.library-header__box {
  padding-top: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 60px;

  @include mq(tablet) {
    margin-bottom: 40px;
  }
}

